      if(!this.changeProductType) {
        for(var j=0;j<this.customTemplateForm.data.length;j++) {
          for(var i = 0;i<this.customTemplateForm.data[j].sub.length;i++) {  
            if(this.customTemplateForm.data[j].sub[i].name == value1){
              this.customTemplateForm.data[j].sub[i].values = this.customTemplateForm.data[j].sub[i].values.substring(0, startPos)+ value +this.customTemplateForm.data[j].sub[i].values.substring(endPos)
            } 
          }
        }    
      }else {
        for(var j=0;j<this.customTemplateForm.data.length;j++) {
          for(var i = 0;i<this.customTemplateForm.data[j].length;i++) {  
            if(this.customTemplateForm.data[j][i].name == value1){
              this.customTemplateForm.data[j][i].values = this.customTemplateForm.data[j][i].values.substring(0, startPos)+ value +this.customTemplateForm.data[j][i].values.substring(endPos)
            } 
          }
        }  
      }
   }, 
    // 点击确定并编辑
    async handleEditTemplate() {
      if(!this.changeProductType) {
        for(var i = 0;i<this.customTemplateForm.data.length;i++) {
          if(this.customTemplateForm.data[i].name !== 'Required') {
            for(var j =0;j<this.customTemplateForm.data[i].sub.length;j++) {
              if(this.customTemplateForm.data[i].sub[j].values =='无') {
                this.customTemplateForm.data[i].sub[j].values = ''
              }
            }
          }
        if(this.customTemplateForm.data[i].name == 'Required') {
          for(var j =0;j<this.customTemplateForm.data[i].sub.length;j++) {
            if(this.customTemplateForm.data[i].sub[j].values ==''&&this.customTemplateForm.data[i].sub[j].name !=='main_image_url'&&this.customTemplateForm.data[i].sub[j].type=='Required') {
              var name = this.customTemplateForm.data[i].sub[j].name
              this.$message({
                type: "error",
                offset: 300,
                message: name+'不能为空'
               });
               return
            }
             if(this.customTemplateForm.data[i].sub[j].values =='无'&&this.customTemplateForm.data[i].sub[j].name !=='main_image_url') {
              var name = this.customTemplateForm.data[i].sub[j].name
              this.$message({
                type: "error",
                offset: 300,
                message: name+'不能为空'
               });
               return
            }
          }
         }
       }
       this.editTemplateLoading = true
       this.customTemplateForm.imgs = []
       for(var i=0;i<this.fileList.length;i++) {
         var obj = {}
         if(i == 0) {
           if(this.fileList[0].type == '1') {
            this.$set(
              obj,
              'main_image_url',
              this.fileList[0].index
            )
            this.$set(
              obj,
              'type',
              this.fileList[0].type
            )
           }else {
             this.$set(
              obj,
              'main_image_url',
              this.fileList[0].url
            )
            this.$set(
              obj,
              'type',
              this.fileList[0].type
            )
           }
            
         }else {
           if(this.fileList[i].type == '1') {
            this.$set(
              obj,
              'other_image_url'+i,
              this.fileList[i].index
            )
            this.$set(
              obj,
              'type',
              this.fileList[i].type
            )
           }else {
             this.$set(
              obj,
              'other_image_url'+i,
              this.fileList[i].url
            )
            this.$set(
              obj,
              'type',
              this.fileList[i].type
            )
           }
            
         }
         this.customTemplateForm.imgs.push(obj)
       }
       if(this.customTemplateForm.sales_type!=='产品变体') {
         this.customTemplateForm.colorNameRegular = ''
         this.customTemplateForm.concatNumber = ''
       }
       let res = await this.api.templateEdit(this.customTemplateForm)
       this.editTemplateLoading = false
       if (res.data.code == "200") {
         this.$message({
           message: "修改成功",
           type: "success"
         });
         this.$router.push("/amazonTemplate");
         this.saveDisabled = false;
       }else {
         this.errorMess('修改失败')
       }
      }else {
        for(var i = 0;i<this.customTemplateForm.data.length;i++) {
          if(this.customTemplateForm.data[i][0].name !== 'Required') {
            for(var j =1;j<this.customTemplateForm.data[i].length;j++) {
              if(this.customTemplateForm.data[i][j].values =='无') {
                this.customTemplateForm.data[i][j].values = ''
              }
            }
          }
        if(this.customTemplateForm.data[i][0].name == 'Required') {
          for(var j =1;j<this.customTemplateForm.data[i].length;j++) {
            if(this.customTemplateForm.data[i][j].values ==''&&this.customTemplateForm.data[i][j].name !=='main_image_url'&&this.customTemplateForm.data[i][j].type=='Required') {
              var name = this.customTemplateForm.data[i][j].name
              this.$message({
                type: "error",
                offset: 300,
                message: name+'不能为空'
               });
               return
            }
             if(this.customTemplateForm.data[i][j].values =='无'&&this.customTemplateForm.data[i][j].name !=='main_image_url') {
              var name = this.customTemplateForm.data[i][j].name
              this.$message({
                type: "error",
                offset: 300,
                message: name+'不能为空'
               });
               return
            }
          }
        }
      }
      this.editTemplateLoading = true
      this.customTemplateForm.imgs = []
      for(var i=0;i<this.fileList.length;i++) {
        var obj = {}
        if(i == 0) {
           this.$set(
             obj,
             'main_image_url',
             this.fileList[0].index
           )
        }else {
           this.$set(
             obj,
             'other_image_url'+i,
             this.fileList[i].index
           )
        }
        this.customTemplateForm.imgs.push(obj)
      }
      if(this.customTemplateForm.sales_type!=='产品变体') {
        this.customTemplateForm.colorNameRegular = ''
        this.customTemplateForm.concatNumber = ''
      }
      let res = await this.api.templateEdit(this.customTemplateForm)
      this.editTemplateLoading = false
      if (res.data.code == "200") {
        this.$message({
          message: "修改成功",
          type: "success"
        });
        this.$router.push("/amazonTemplate");
        this.saveDisabled = false;
      }else {
        this.errorMess('修改失败')
      }
      }
     
    },
    imgUpload() {
      //  this.$refs.customTemplateFormRef.validate((valid) => {
      //  if(valid) {
      //  if(this.customTemplateForm.imgs =='') {
      //    this.$message({
      //         message: '上传图片不能为空',
      //         type: 'error'
      //    })
      //    return false
      //  }
      //自定义的上传图片的方法
      // 1. 创建formData 利用AXIOS传递
      this.saveDisabled = true;
      let _this = this;
      if (_this.imageList.length == 0) {
        this.handleEditTemplate();
      } else {
        let formData = new FormData();
        formData.append("files", this.files);
        for (let index = 0; index < _this.imageList.length; index++) {
          const element = _this.imageList[index].raw;
          formData.append("image[]", element);
        }
        this.imgRequest("/v1/upLoadimgs", formData)
          .then(res => {
            if (res.data.code == 200) {
              if (this.customTemplateForm.imgs !== "") {
                this.customTemplateForm.imgs =
                  this.customTemplateForm.imgs + "," + res.data.data;
              } else {
                this.customTemplateForm.imgs = res.data.data;
              }

              this.handleEditTemplate();
            }
          })
          .catch(error => {
            this.saveDisabled = false;
          });
      }
      //  }
      //  })
    },
    // 产品类型改变
    productTypeChange(value) {
      this.changeProductType = true
      this.varibleInfo = this.customTemplateForm.all_excel_message[value];
      this.productVaribles = this.customTemplateForm.all_excel_message[value];
      this.customTemplateForm.data =  this.productVaribles
    },
    // 获取详情信息 
    async getTemplateDetailInfo(id) {
      this.editTemplateLoading = true
      let res = await this.api.templateDetails({id: id})
      if (res.data.code == "200") {
        this.customTemplateForm = res.data.data;
        this.productTypeList = res.data.data.productTypeList
        this.$set(this.customTemplateForm, id, this.templateId);
        if(this.customTemplateForm.colorNameRegular==null) {
          this.customTemplateForm.colorNameRegular = ''
        }
        for(var i = 0;i<this.customTemplateForm.imgs.length;i++) {
          if(i==0) {
            if(this.customTemplateForm.imgs[0].type == '1') {
              this.fileList.push({index: this.customTemplateForm.imgs[0].main_image_url,type:this.customTemplateForm.imgs[0].type,url :''})
            } else {
              this.fileList.push({index: this.customTemplateForm.imgs[0].main_image_url,type:this.customTemplateForm.imgs[0].type,url :this.customTemplateForm.imgs[0].main_image_url})
            }
          }else {
            if(this.customTemplateForm.imgs[i].type == '1') {
              this.fileList.push({index: this.customTemplateForm.imgs[i]['other_image_url'+i],type:this.customTemplateForm.imgs[i].type,url :''})
            }
            else {
              this.fileList.push({index: this.customTemplateForm.imgs[i]['other_image_url'+i],type:this.customTemplateForm.imgs[i].type,url :this.customTemplateForm.imgs[i]['other_image_url'+i]})
            }
            
          }      
        }
        console.log(this.fileList);
      
        
        this.productVaribles = this.customTemplateForm.data;
        this.varibleInfo = this.customTemplateForm.excel_message;
        for (var i = 0; i < this.varibleInfo.length; i++) {    
          for (var j = 0; j < this.varibleInfo[i].length; j++) {
            if(this.varibleInfo[i][0].name == 'Required') {
              for(var a = 0;a< this.varibleInfo[i].length;a++) {
                for(var s = 0;s<this.productVaribles.length;s++) {
                  if(this.productVaribles[s].name == 'Required') {
                    for(var y = 0 ;y<this.productVaribles[s].sub.length;y++) {
                      if(this.productVaribles[s].sub[y].name == this.varibleInfo[i][j].name) {
                       this.$set(this.productVaribles[s].sub[y],'type',this.varibleInfo[i][j].type) 
                      }
                    }
                  }
                }
              }
              
            }
            if (this.varibleInfo[i][j].params) {
              for (var k = 0; k < this.productVaribles.length; k++) {
                for (
                  var n = 0;
                  n < this.productVaribles[k].sub.length;
                  n++
                ) {
                  if (
                    this.productVaribles[k].sub[n].name ==
                    this.varibleInfo[i][j].name
                  ) {
                    this.productVaribles[k].sub[
                      n
                    ].params = this.varibleInfo[i][j].params;
                  }
                }
              }
            }
          }
        }
      }
      this.editTemplateLoading = false
      
    },
    // 获取店铺列表
    async getStoreList() {
      const res = await this.api.stoLists()
      if(res.data.code == 200) {
        this.storeList = res.data.data
        this.customTemplateForm.store_name = this.storeList[0].id
      }
    },
  },
  created() {
    this.getStoreList()
    this.changeProductType = false
    this.getCountryList();
    this.templateId = this.$route.query.id;
    this.getTemplateDetailInfo(this.templateId);
    
  },
  computed: {
    length() {
      return this.fileList.length;
    }
  }
};
</script>
